import React from 'react';
import Typing from 'react-typing-animation';

const Header = () => {
    return (

        <div style={{textAlign: "center", backgroundColor: "black"}}>
            <Typing>
                <span style={{color: "yellow"}}><h1>StarWars Refuel Calculator</h1></span>
            </Typing>
        </div>
    );
};

export default Header;

