import React, {Component} from 'react';
import axios from "axios";


class StarShipGrid extends Component {

    state = {
        next: null,
        previous: null,
        results: [],
        distance: 0
    };

    getPageData = async (e) => {
        e.preventDefault();
        let id = e.target.id;
        let apiParams;
        if (id === "calculate") {
        apiParams = {
            endpoint: null,
            distance: this.state.distance
        };
    } else if(id === "next") {
            apiParams = {
                endpoint: this.state.next,
                distance: this.state.distance
            };
        } else {
            apiParams = {
                endpoint: this.state.previous,
                distance: this.state.distance
            };
        }
        const request =  await axios.post("https://starwars-refuel-calculator.herokuapp.com/api/starship/refuels", apiParams);
        this.setState({next: request.data.next, previous: request.data.previous, results: request.data.results});
    };

    renderNextButton() {
        if (this.state.next != null) {
            return (
                <button onClick={this.getPageData} id="next" style={{color: "black", backgroundColor: "yellow"}}>Next</button>
            );
        }
    };

    renderPreviousButton() {
        if (this.state.previous != null) {
            return (
                <button onClick={this.getPageData} id="previous" style={{color: "black", backgroundColor: "yellow"}}>Previous</button>
            );
        }
    };

    handleDistanceChange = event => {
        this.setState({ distance: event.target.value });
    };

    handleReturnKey = async event => {
        if (event.key === 'Enter') {
            event.preventDefault();
            let apiParams = {
                endpoint: null,
                distance: this.state.distance

            };

            const request =  await axios.post("https://starwars-refuel-calculator.herokuapp.com/api/starship/refuels", apiParams);
            this.setState({next: request.data.next, previous: request.data.previous, results: request.data.results});
        }
    };

    render() {

        return (
            <div style={{textAlign: "center", backgroundColor: "black"}}>
                <h3 style={{textAlign: "center", color: "yellow"}}>Enter The Distance To Calculate</h3>
                <form style={{textAlign: "center", color: "yellow"}}>

                    <input onKeyPress={this.handleReturnKey} onChange={this.handleDistanceChange} type="text" name="distance"></input>
                    <button id="calculate" onClick={this.getPageData} style={{color: "black", backgroundColor: "yellow"}}>Calculate</button>
                </form>
                <table style={{marginLeft: "15%",width: "70%",marginReft: "15%", marginTop: "2%", color: "yellow", borderColor: "yellow", border: "1px solid red"}}>
                    <tr>
                      <th style={{border: "1px solid white"}}>StarShip</th>
                        <th style={{border: "1px solid white"}}>MGLT</th>
                        <th style={{border: "1px solid white"}}>Consumables</th>
                        <th style={{border: "1px solid white"}}>Refuel Count</th>
                    </tr>
                    {this.state.results.map((item) => (
                        <tr>
                            <td>{item.name}</td>
                            <td>{item.MGLT}</td>
                            <td>{item.consumables}</td>
                            <td>{item.refuelCount}</td>
                        </tr>
                    ))}
                </table>
                <div style={{textAlign: "center", marginTop: "2%"}}>
                    {this.renderPreviousButton()}
                    {this.renderNextButton()}
                </div>
            </div>);
    }
};
export default StarShipGrid;